/* universal */

* {
  box-sizing: border-box;
  margin: 0 auto;
}

html,
body {
  font-size: 16px;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  background-color: #201f2e;
}

/* Navbar and nav-menu */

.navbar {
  background: linear-gradient(
    90deg,
    rgb(242, 242, 242) 0%,
    rgb(243, 243, 243) 100%
  );
  color: #201f2e;
  height: 4.75rem;
  width: 100%;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0%;
  z-index: 999;
  text-decoration: none;
}

.nav-home-container {
  height: 4rem;
  display: flex;
  align-items: center;
  position: fixed;
  justify-content: left;
  font-size: 2rem;
  z-index: 4;
}

.home-button {
  color: #201f2e;
  text-decoration: none;
  padding: 10px 20px;
  display: flex;
  text-align: left;
  margin-left: 1rem;
}

.menu-icon {
  display: flex;
  width: 80px;
  position: fixed;
  right: 0%;
  text-align: right;
  justify-content: center;
}

.nav-menu-button {
  display: flex;
  color: #fffe;
  cursor: pointer;
  margin-right: 1rem;
}

.fa-bars {
  color: #201f2e;
  margin-right: 3rem;
  padding: 10px 20px;
  justify-content: center;
}

#bars {
  font-size: 2rem;
  color: #201f2e;
}

.nav-menu {
  position: relative;
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 1.5rem;
  list-style: none;
  text-align: center;
  width: 75vw;
  justify-content: end;
  margin-right: 2rem;
  left: -200%;
  top: -100%;
  transition: all 0.2s ease-in-out;
}

.nav-button {
  text-decoration: none;
  color: #201f2e;
  font-size: 1.5rem;
  display: block;
  width: 100%;
}

.nav-item {
  display: flex;
  color: #fffe;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem;
  height: 4.5rem;
  cursor: pointer;
  width: 100%;
  justify-items: center;
  font-weight: bolder;
}

.menu-icon {
  display: inline;
}

/* Main body */

.main-container {
  background-color: #201f2e;
  color: #fffe;
}

/* About me */

.about-me {
  padding: 6vw 6vw 0 6vw;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(1, 88vw);
  grid-auto-rows: minmax(8vh, auto);
  align-items: center;
  justify-items: center;
}

h1 {
  font-size: 2.25rem;
  display: inline-flex;
  flex: auto;
  margin-bottom: 2rem;
  grid-column: 1 / 2;
  text-align: center;
}

.circular-portrait {
  position: relative;
  width: 250px;
  max-height: 250px;
  overflow: hidden;
  border-radius: 50%;
  grid-column: 1 / 2;
  grid-row: 2 / 4;
}

.circular-portrait img {
  width: 100%;
  height: auto;
  display: flex;
  flex: auto;
  size: 50%;
}

.about-me h2 {
  padding-top: 6rem;
  margin: 0;
  grid-column: 1 / 2;
  grid-row: 5 / 6;
  font-size: 2.5rem;
  display: flex;
  flex: auto;
  margin-bottom: 0;
}

.about-me p {
  box-sizing: border-box;
  width: 80%;
  grid-column: 1 / 2;
  line-height: 2.5rem;
  font-size: 1rem;
  display: flex;
  flex: auto;
  text-align: left;
  padding: 2rem 1rem 0 1rem;
}

#quote {
  margin-top: 1rem;
  padding: 6rem 0;
  font-family: "Times New Roman", Times, serif;
  font-size: 1.5rem;
}

/* skills */

.skills {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
}

.skills h2 {
  margin-top: 6rem;
  grid-column: 1 / 2;
  font-size: 2.5rem;
  display: flex;
  flex: auto;
  margin-bottom: 3rem;
}

.skill-set {
  text-align: center;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.skill-container {
  flex-direction: column;
  flex: 1;
}

.timer-container {
  margin-top: 4rem;
}

.fab,
.fas {
  font-size: 4rem;
  padding: 1rem;
}

/* projects */

.projects {
  width: 80%;
  margin-top: 4rem;
  padding-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-items: center;
  grid-gap: 2vw;
  text-align: center;
}

.projects h2 {
  font-size: 2.5rem;
  display: flex;
  flex: auto;
  margin-top: 4rem;
  margin-bottom: 2rem;
  justify-content: center;
}

.projects h3 {
  padding-left: 4vw;
  padding-right: 4vw;
  padding-bottom: 1rem;
}

.projects h4 {
  padding: 1rem 0;
}

.project {
  display: block;
  margin-top: 3rem;
  font-size: 1.25rem;
  border: 2px solid #fffe;
  border-radius: 5%;
  justify-content: center;
  text-align: center;
  padding: 1rem;
}

.project-segment {
  text-align: center;
}

#tmc-project {
  cursor: pointer;
}

.image-container {
  max-width: 90%;
  max-height: 70vh;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  overflow: auto;
}

.mobile-image-container {
  width: 70vw;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  max-height: 75vh;
  width: 70vw;
  overflow: auto;
}

.card-pic {
  max-width: 100%;
  height: auto;
}

.mobile-card-pic {
  flex: 1;
  max-width: 30%;
}

/* footer */

footer {
  margin-top: 2rem;
  padding: 6vw;
  background-color: #0b925e;
  display: grid;
  grid-template-columns: repeat(1, 88vw);
  grid-auto-rows: minmax(10vh, auto);
  text-align: center;
  align-items: center;
  justify-items: center;
}

.footer-button {
  font-size: 2rem;
  text-decoration: none;
  width: fit-content;
  color: #fffe;
  padding: 6vw;
  border: 2px solid #fffe;
  border-radius: 5%;
  cursor: pointer;
}

/* contact.html css */

.contact {
  padding-left: 6vw;
  display: grid;
  grid-template-columns: repeat(1, 88vw);
  grid-auto-rows: minmax(10vh, auto);
  height: auto;
  min-width: 100%;
  margin: 0 auto;
  font-size: 3rem;
  grid-gap: 20px;
  align-items: center;
  justify-items: center;
}

.contact-list {
  border: 2px solid #fffe;
  border-radius: 10%;
  justify-content: center;
  text-align: center;
  width: 60%;
}

.cont-email {
  font-size: 3rem;
  color: #fffe;
  padding: 1.5rem;
  text-decoration: none;
}

.link {
  color: #fffe;
  text-decoration: none;
  display: block;
  cursor: pointer;
}

@media (hover: hover) {
  .project:hover {
    background-color: #fffe;
    color: #201f2e;
    border: none;
    transition: all 0.2s ease-in-out;
  }

  .home-button:hover,
  .nav-button:hover,
  .fa-bars:hover {
    color: #201f2eb0;
  }

  .footer-button:hover {
    background-color: #fffe;
    color: #201f2e;
    border: 2px solid #201f2e;
    transition: all 0.2s ease-in-out;
  }

  .contact-list:hover {
    background-color: #fffe;
    border: none;
    transition: all 0.2s ease-in-out;
  }

  .link:hover {
    color: #201f2e;
  }

  .nav-links:hover {
    border-bottom: 4px solid #201f2e;
    transition: all 0.2s ease-out;
  }
}

@media screen and (min-width: 900px) {
  /*ONLY USE HOVER ON DESKTOP DEVICES*/
  .menu-icon {
    display: none;
  }

  .nav-button {
    font-size: 1.75rem;
    font-weight: bolder;
  }

  .home-button {
    font-size: 2.25rem;
    font-weight: bolder;
  }

  .nav-item {
    width: 140px;
    margin-top: 5px;
  }

  .nav-menu {
    position: relative;
    left: auto;
    display: grid;
    flex-direction: row;
    text-align: center;
    align-content: center;
    grid-template-rows: 80px;
    grid-auto-columns: repeat(12, auto);
    top: 0;
  }

  .name {
    font-size: 2.5rem;
    padding: 2rem;
  }
}

@media screen and (prefers-reduced-motion: no-preference) {
  html,
  body {
    scroll-behavior: smooth;
  }
}
